.black-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: black;
  color: white;
}



.menu {
  display: flex;
  flex-direction: column;
  background-color: white;
  position: absolute;
  top: 50px;
  right: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}


.App {
  padding: 20px;
}

.list {
  padding: 10px;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  border-radius: 5px;
}

.list h4 {
  cursor: pointer;
}

.list span {
  margin-right: 10px;
  cursor: pointer;
}

.modal {
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 10px;
  background-color: #f9f9f9;
  margin-top: 20px;
}
