.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: black;
  color: white;
}

.logo {
  font-size: 24px;
}

.hamburger {
  font-size: 24px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  display: none; /* 기본적으로 숨기기 */
}

.menu {
  display: none;
  position: absolute;
  top: 50px;
  right: 10px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.menu.open {
  display: block;
}

.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu li {
  border-bottom: 1px solid #ccc;
}

.menu li:last-child {
  border-bottom: none;
}

.menu a {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: black;
}

.menu a:hover {
  background-color: #f0f0f0;
}

.default-nav {
  display: flex;
}

.default-nav ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.default-nav li {
  margin-right: 20px;
}

.default-nav a {
  text-decoration: none;
  color: white;
}

.section {
  padding: 20px;
}

/* 모바일 화면에서 햄버거 메뉴 표시 및 기본 네비게이션 숨기기 */
@media (max-width: 768px) {
  .hamburger {
    display: block;
  }

  .default-nav {
    display: none;
  }
}

